interface Status {
	TODO: string;
	DONE: string;
	IN_PROGRESS: string;
}

const STATUS: Status = {
	TODO: "To do",
	DONE: "Done",
	IN_PROGRESS: "In Progress",
};

interface Priority {
	HIGH: string;
	LOW: string;
}

const PRIORITY: Priority = {
	HIGH: "High",
	LOW: "Low",
};

interface Task {
	status: string;
	priority: string;
	task: string;
}

const toDoList: Array<Task> = [];

function addTask(task: string, status: string = STATUS.TODO, priority: string = PRIORITY.HIGH) {
	const index = toDoList.findIndex((obj) => obj.task === task);

	if (index === -1) {
		toDoList.push({ status, priority, task });
	}
}

function deleteTask(task: string) {
	const index = toDoList.findIndex((obj) => obj.task === task);

	if (index !== -1) {
		toDoList.splice(index, 1);
	}
}

function changeStatus(task: string, status: string) {
	toDoList.map((obj) => {
		if (obj.task === task) {
			obj.status = status;
		}
	});
}

function changePriority(task: string, priority: string) {
	toDoList.map((obj) => {
		if (obj.task === task) {
			obj.priority = priority;
		}
	});
}

addTask("сходить за водой");
addTask("сходить в зал");
addTask("Сходить в магазин");
deleteTask("сходить в зал");
changeStatus("сходить за водой", STATUS.DONE);
addTask("почитать книгу");
changePriority("почитать книгу", PRIORITY.LOW);

console.log(toDoList);
